*{
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  text-decoration: none;
}

.profile-bg{
  position: relative;
  width: 100%;
  min-width: 480px;
  height: 100vh;
  min-height: 500px;
  background: url(https://i.imgur.com/OxApF88.jpg) no-repeat center;
  background-size: cover;
}

#btn-swipe{
  position:absolute;
  background: #3afbfe90;
  border: none;
  outline: none;
  width: 50px;
  height: 50px;
  right: 6%;
  bottom: 6%;
  margin: 0px;
  border-radius: 25px;
  transition: all .5s, height .01s;
}

#btn-swipe img{
  width: 50px;
  height: 50px;
  border-radius: 25px;
  box-shadow: 0px 0px 4px 4px #3afbfe90;
}

#btn-swipe:hover{
  background: #3afbfe;
  right: 6%;
  bottom: 7%;
  margin: 0px;
  border-radius: 25px;
  box-shadow: 0px 0px 6px 4px #3afbfe;
}

#btn-swipe:active{
  box-shadow: 0px 0px 6px 4px #3afbfe;
  height: 45px;
}

.profile-card{
  width: 85vw;
  max-width: 500px;
  min-width: 400px;
  transform: translate(-50%, -50%);
  position: relative;
  text-align: center;
  border-radius: 100px;
  overflow: hidden;
  background: linear-gradient(-60deg, #080b2eea, #0b0222f3);
  padding: 40px 0px 20px 0px;
  box-shadow: 5px 8px 10px 7px black;
  transition: all .5s ease-in-out;
  left: 50%;
  top: 50%;
}

.profile-card:hover{
  transform: translate(-52%, -52%);
  box-shadow: 11px 14px 20px 9px black;
  padding: 43px 3px 23px 3px;
}

#profilePhoto{
  width: 200px;
  height: 200px;
  border-radius: 50%;
  box-shadow: 0px 0px 7px 5px #3afbfe;
}

.name{
  color: #f2f2f2;
  font-size: 28px;
  font-weight: 600;
  margin: 10px 0;
}

.description{
  font-size: 18px;
  color: #e66767;
}

.social-bar{
  display: flex;
  justify-content: center;
  margin: 20px;
}

.link-btn{
  display: inline-block;
  width: auto;
  height: 40px;
  background: white;
  box-shadow: 0px 0px 5px 3px #3afbfe;
  border-radius: 20px;
  outline: none;
  border: none;
  margin: 10px;
  transition: width .5s ease-in-out, color .3s;
}

.btn-icon{
  width: 36px;
  height: 36px;
  padding: 2px;
  outline: none;
  border: none;
}

.btn-text{
  max-width: 0;
  display: inline-block;
  -webkit-transition: color 1s, max-width .5s, padding .5s;
  transition: color 1s, max-width .5s, padding .5s;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
  color: white;
  line-height: 40px;
}

.link-btn:hover{
  width:auto;
  transition: width .5s ease;
}

.link-btn:hover .btn-text{
  display: inline-block;
  max-width: 100px;
  color: black;
  -webkit-transition: all 1s;
  transition: all 1s;
  line-height: 40px;
  padding: 0px 10px 0px 0px;
}



/* PROJECTS STYLE */

@font-face {
  font-family: myFont;
  src: url('./components/DS-DIGIB.TTF') format('truetype');
}


.projects{
  width: 100%;
  min-width: 480px;
  height: 100vh;
  min-height: 500px;
  background: url(https://i.imgur.com/qYoFSV5.jpg) no-repeat center;
  background-size: cover;
}

.projects h1{
  text-align: center;
  display: inline-block;
  position: relative;
  font-family: myFont;
  font-size: 40px;
  transform: translate(-50%, -50%);
  background-color: #3afbfe;
  border-radius: 5px;
  padding: 3px;
  left: 50%;
  top: 15%;
}

.slidershow{
    width: 80vw;
    max-width: 700px;
    min-width: 450px;
    height: 70%;
    max-height: 400px;
    min-height: 300px;
    overflow: hidden;
    position: relative;
    border-radius: 25px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0px 0px 8px 5px  #3afbfe;
    transition: all .5s ease-in-out;
  }

  .navigation{
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    background-color: #0005;
    border-radius: 10px;
    transition: all .3s;
  }

  .navigation:hover{
    background-color: #000A;
  }
  .bar{
    width: 20px;
    height: 20px;
    border: 1px solid #000C;
    margin: 6px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.4s;
    background-color: #FFFA;
  }
  .bar:hover{
    background: green;
    border: 2px solid #000C;
    width: 22px;
    height: 22px;
  }
  
  input[name="r"]{
      position: absolute;
      visibility: hidden;
  }
  
  .slide img{
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
  }

  .description-bar{
    position: absolute;
    z-index: 3;
    transform: translateX(-50%);
    top: 20px;
    left: 50%;
    width: 90%;
    height: 150px;
    background-color: #030E51;
    border-radius: 20px;
    opacity: 0;
    transition: opacity 1s ease;
    box-shadow: 0px 0px 5px 5px #030E51;
  }

  .slidershow:hover .description-bar{
    opacity: 1;
  }

  .description-bar h2{
    margin: 15px 0px 5px 20px;
    color: #e66767;
  }

  .description-bar p{
    margin: 17px 0px 5px 20px;
    color:white;
    width: 93%;
    height: 65%;
  }
  .description-bar a {
    position: absolute;
    margin:0px;
    width: 120px;
    height: 30px;
    color: #030E51;
    background-color: #e66767;
    box-shadow: 2px 2px 1px 1px  #3afbfe;
    border-radius: 30px;
    font-size: larger;
    font-weight: bold;
    line-height: 30px;
    text-align: center;
    right: -25px;
    top: 10px;
    transform: translateX(-50%);
    text-shadow: 1px 1px 2px #3afbfe;
    transition:all .5s ;
  }

  .description-bar a:hover {
    box-shadow: 6px 5px 4px 1px  #3afbfe;
    text-shadow: 1px 1px 4px #3afbfe;
    right: -22px;
    top: 8px;
    width: 122px;
    height: 32px;
    line-height: 32px;
  }